@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 100%;
}
.page {
    height: 100vh;
    position: fixed;
    width: 100%;
    top: 0;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}

.paletteList.page-enter {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
}

.paletteList.page-enter-active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.paletteList.page-exit-active {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
}



.newPalette.page-enter {
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
}

.newPalette.page-enter-active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.newPalette.page-exit-active {
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
}


.paletteForward.page-enter {
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
}

.paletteForward.page-enter-active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.paletteForward.page-exit-active {
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
}

.singleColorPalette.page-enter {
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
}

.singleColorPalette.page-enter-active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.singleColorPalette.page-exit-active {
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
}

/* //////////////////// */
.paletteBack.page-exit-active {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
}

.paletteBack.page-enter {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
}

.paletteBack.page-enter-active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

